.opacityFone1 {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 15;
}
.opacityFone1 button {
  max-width: 100px;
}
.modalContent1 {
  width: max-content;
  padding: 20px 30px;
  background-color: rgba(0, 0, 0, 0.85);
  border: 1px solid wheat;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 5;
  min-height: 30vh;
  max-height: max-content;
  padding: 50px 100px;
}
.showCurrentVariants {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  position: relative;
}
.next {
  position: absolute;
  bottom: 0;
  right: 0;
  cursor: pointer;
}
.finalVariants {
  display: flex;
  align-items: center;
  gap: 50px;
}

.sectionResultLeft {
  overflow-y: scroll;
  padding-right: 50px;
  max-height: 70vh;
}
.sectionResultLeft::-webkit-scrollbar {
  width: 5px;
}

.sectionResultLeft::-webkit-scrollbar-track {
  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: #f9f9fd;
  border-radius: 10px;
}

.sectionResultLeft::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: linear-gradient(180deg, #d9e836, #8a960e);
}
.mainResult {
  display: flex;
  justify-content: space-between;
}
.sectionResultRight {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.sectionResultRight p{
  max-width: 350px;
}
.percent {
  position: relative;
  width: 280px;
  height: 280px;
  margin: 15px auto 0 auto;
}

.percent svg {
  position: relative;
  width: 280px;
  height: 280px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.percent h2 {
  position: absolute;
  right: 130px;
  top: 76px;
}

.percent svg circle {
  width: 280px;
  height: 280px;
  fill: none;
  stroke-width: 18;
  stroke: #000;
  transform: translate(10px, 10px);
  stroke-dasharray: 692;
  /* stroke-dashoffset: 520; */
}

.percent svg circle:nth-child(1) {
  stroke-dashoffset: 0;
  stroke: #d9e836;
}
.percent svg circle:nth-child(2) {
  stroke: rgba(0, 0, 0, 0.8);
}

.textAreaWrapper {
  max-width: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.textAreaWrapper h3 {
  text-align: center;
  margin: 0;
}
.textAreaWrapper h6 {
  align-self: flex-start;
  color: rgb(226, 6, 6);
  margin: 0;
}
#freeResponse {
  background-color: transparent;
  border-radius: 10px;
  color: white;
  padding: 10px;
}
.sendButtonFreeResponse {
 min-width: 200px;
 
}
@keyframes shake {
  0% {
    transform: translate(2px, 2px);
  }
  25% {
    transform: translate(-2px, -2px);
  }
  50% {
    transform: translate(2px, 2px);
  }
  75% {
    transform: translate(-2px, -2px);
  }
  100% {
    transform: translate(0, 0);
  }
}
