.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  gap: 80px;
  position: relative;
  overflow: hidden;
  background-image: url("./components/Image/doctor_room.jpg");
  /* background-image: url('./components/Image/doctor\ office.jpg'); */
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
}
.menuButtons {
  position: absolute;
  /* background-color: #8a960e; */
  /* background: linear-gradient(180deg, #d9e836, #8a960e); */
  background: linear-gradient(10deg, #502c0286, #f7ca6a94);
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  top: 50px;
  left: 50px;
  gap: 10px;
  z-index: 2;
}
.hint {
  position: absolute;
  top: -25px;
  right: -25px;
  font-size: 18px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: linear-gradient(10deg, #502c02, #f7ca6a);
  cursor: pointer;
  color: black;
}
.hintInfo {
  position: absolute;
  right: -200px;
  top: 50px;
  background-color: #252525;
  padding: 5px 10px;
  border-radius: 5px;
  color: white;
}
.menuButtons button {
  cursor: pointer;
  border: none;
  font-size: 24px;
  padding: 4px 10px;
  /* background: linear-gradient(10deg, #252525, white); */
  background: linear-gradient(180deg, #502c02, #f7ca6a);
  border-radius: 5px;
  transition: 0.4s;
}
.menuButtons button:hover {
  background: #502c02;
  color: #f9f9fd;
  transition: 0.4s;
}
.menuButtons button:disabled {
  color: #252525;
  background: transparent;
}
.dialogInputs {
  position: absolute;
  top: 0;
}

.doctorPerson {
  width: max-content;
  position: absolute;
  bottom: -30px;
  margin-left: 600px;
}
.doctorPerson img {
  width: 450px;
}
.patientPerson {
  width: max-content;
  position: absolute;
  bottom: -45px;
  margin-right: 700px;
}
.patientPerson img {
  width: 450px;
}
.dialogs {
  position: absolute;
  background-color: #252525c2;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  width: 350px;
  height: 90vh;
  transition: 1s;
}
.dialogsContent {
  overflow-y: scroll;
  padding-right: 15px;
  padding-left: 15px;
  padding-top: 15px;
}
.dialogsContent::-webkit-scrollbar {
  width: 5px;
}

.dialogsContent::-webkit-scrollbar-track {
  -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  background-color: #f9f9fd;
  border-radius: 10px;
}

.dialogsContent::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: linear-gradient(180deg, #d9e836, #8a960e);
}
.openDialogsButton {
  position: absolute;
  color: white;
  border: none;
  background-color: transparent;
  font-size: 24px;
  cursor: pointer;
}
.poweredByMeroPlace {
  text-align: center;
  background-color: #252525;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border-radius: 10px;
  margin-top: 20px;
}
.poweredByMeroPlace img {
  width: 90px;
}

.mouse {
  animation: talk 0.3s linear infinite;
}
@keyframes talk {
  0% {
    height: 0;
  }
  100% {
    height: 10px;
  }
}
.pulsHint {
  animation: pulsHint 1s linear infinite;
}
@keyframes pulsHint {
  0% {
    transform: scale(1, 1);
    box-shadow: 0 0 0px red;
  }
  25% {
    transform: scale(1.1, 1.1);
    box-shadow: 0 0 7px red;
  }
  50% {
    transform: scale(1, 1);
    box-shadow: 0 0 0px red;
  }
  75% {
    transform: scale(1.1, 1.1);
    box-shadow: 0 0 7px red;
  }
  100% {
    transform: scale(1, 1);
    box-shadow: 0 0 0px red;
  }
}
@media (max-width: 1700px) {
  .menuButtons {
    opacity: 0.8;
  }
  .patientPerson {
    margin-right: 350px;
  }
  .doctorPerson {
    margin-left: 700px;
  }
}

@media (max-width: 1000px) {
  .App {
    flex-direction: column;
    gap: 50px;
    height: 100vh;
  }
  .pullQuestions {
    overflow-y: scroll;
    max-height: 50vh;
  }
}
