.opacityFone {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 15;
}
.opacityFone button {
  max-width: 100px;
}
.sendButton {
  border: none;
  padding: 5px 15px;
  border-radius: 10px;
  cursor: pointer;
  background: linear-gradient(180deg, #d9e836, #8a960e);
  transition: 0.8s;
}
.sendButton:hover {
  background: linear-gradient(180deg, #535814, #444907);
  color: white;
  transition: 0.8s;
}

.modalContent {
  width: 60%;
  padding: 20px 30px;
  background-color: rgba(0, 0, 0, 0.85);
  border: 1px solid wheat;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 5;
}
.startButton {
  background-color: antiquewhite;
  border: none;
  width: 80px;
  height: 25px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 70px;
}
.diagnosisContent {
  display: flex;
  gap: 50px;
  align-items: baseline;
  justify-content: space-around;
  margin-bottom: auto;
}
.charter {
  cursor: pointer;
  width: max-content;
  padding: 0 15px;
  height: 40px;
  border-radius: 10px;
  transition: 0.5;
  opacity: 0;
  animation: showElement 0.6s linear forwards;
}
@keyframes showElement {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}
.showElement {
  opacity: 1;
}
.variants {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 10px;
}
.variants span {
  position: relative;
  border: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 2px 5px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 14px;
  min-height: 50px;
}
@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.treatmentContent {
  padding: 0 50px;
}
.headerTreatment {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}
.headerTreatment div {
  font-size: 24px;
  cursor: pointer;
}
.treatmentMainContent {
  display: flex;
  flex-direction: column;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
}
@keyframes welcomeMedicalGroup {
  0% {
  }
  100% {
    opacity: 1;
    transform: translate(0 0);
  }
}
@keyframes welcomeMedCurrent {
  0% {
  }
  100% {
    opacity: 1;
  }
}

.treatmentFlex {
  display: flex;
  margin: 40px 0;
  justify-content: center;
}
.currentMed {
  background-color: black;
  border-radius: 0 30px 30px;
  border: 1px solid white;
  width: 60%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 100px 100px 100px 100px 100px;
  gap: 10px;
  padding: 10px;
  margin-left: -1px;
}
.currentMed span {
  border: 1px solid white;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  cursor: pointer;
  padding: 0px 10px;
}
.nonMedicalTreatmentPath {
  text-align: center;
  border: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  cursor: pointer;
  position: relative;
}
@media (max-width: 1500px) {
  .modalContent {
    width: 80%;
  }
}
@media (max-width: 1000px) {
  .modalContent {
    height: 80%;
  }
}
@media (max-width: 500px) {
  .modalContent {
    width: 80%;
  }
}
