.opacityFone2 {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 15;
}
.opacityFone2 button {
  max-width: 100px;
}
.modalContent2 {
  width: 40%;
  padding: 20px 30px;
  background-color: rgba(0, 0, 0, 0.85);
  border: 1px solid wheat;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 5;
  min-height: 30vh;
  max-height: max-content;
  padding: 50px 100px;
}
.mainInstrumental {
  display: flex;
  justify-content: center;
  gap: 100px;
}

.instrumentalVariants {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.instrumentalAnswers {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.imgWrapper {
  position: absolute;
  border: 10px solid white;
  z-index: 11;
  display: flex;
  justify-content: center;
  align-items: center;
}
.showInstrumentalImage {
  border: 1px solid white;
  text-align: center;
  border-radius: 5px;
  cursor: pointer;
}
