.pullQuestionsWrapper {
  position: absolute;
  right: 50px;
  bottom: 30px;
  background: linear-gradient(10deg, #502c0298, #f7ca6a8f);
  border-radius: 10px;
  padding: 0 10px 10px 10px;
}
.pullQuestionsWrapper h2 {
  text-align: center;
  color: black;
  margin-bottom: 10px;
  background: linear-gradient(10deg, #502c02, #f7ca6a);
  border-radius: 5px;
  margin-top: 5px;
  font-size: 28px;
}
.pullQuestions {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.question {
  cursor: pointer;
}
.clouseQuestionsAndNext {
  cursor: pointer;
  position: absolute;
  top: -40px;
  border: none;
  font-size: 20px;
  background: linear-gradient(10deg, #502c02, #f7ca6a);
  border-radius: 10px;
  padding: 5px 10px;
  animation: puls 0.9s linear infinite;
}
@keyframes puls {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.1, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
